/* 전체 레이아웃 */
#rent-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 20px;
    min-width: 1300px;
    min-height: 570px;
    background-color: white;
}

/* 탑 패널 */
.rent-top-box {
    display: flex;
    flex-direction: row;
    width: 1700px;
    background-color: #e0e0e0;
    border-radius: 10px;
    justify-content: center;
}

.rent-top-bar-container {
    display: flex;
    flex-direction: row;
    gap: 20px;
    padding: 30px;
    width: 80%;
    justify-content: space-between;
    align-items: center;
}

.rent-top-bar-rent-spot, .rent-top-bar-return-spot{
    display: flex;
    gap: 10px;
    height: 140px;
    font-size: 20px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* 검색하기 or 새로고침 버튼 css */
.search-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.button-class-role {
    background-color: rgb(38, 133, 197); 
    width: 200px; 
    height: 50px; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    border-radius: 10px; 
    font-family: Arial, sans-serif; 
    font-size: 16px; 
    color: white; 
    border: none; 
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: background-color 0.3s ease; 
}

.button-class-role:hover {
    background-color: rgb(20, 107, 165);
}

/* 검색 결과 영역 */
.rent-result {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 1700px;
}

.rent-content {
    display: flex;
    gap: 20px;
    width: 100%;
    height: 100%;
}

/* 아이템 선택 박스 */
.rent-item {
    flex: 1;
    padding: 20px;
    background-color: #e0e0e0;
    border-radius: 10px;
    height:  100%;
}

/* 장바구니 및 결제 */
.rent-right-side {
    width: 400px;
    padding: 20px;
    background-color: #e0e0e0;
    border-radius: 10px;
    position: sticky;
    top: 20px;
    gap: 20px;
    align-self: flex-start;
    
    min-height: 275px;
}

.rent-right-side-top-basket {
    text-align: center;
    font-family: "Noto Sans KR";
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 20px;
}